button {
 font-size: 30px;
}
#top {
  display: block;
  width: 100%;
  position: absolute;
  text-align: center;
  z-index: 1;
  bottom: 1em;
}
#bottom {
  position: absolute;
  top: 50%;
  margin-top: -135px;
  left: 50%;
  margin-left: -135px;
  color: white;
}

#colorDisplay.red {
  border-color: red;
  color: red;
}

#colorDisplay {
  margin: auto;
  color: white;
  width: 250px;
  height: 250px;
  border: 10px solid white;
  border-radius: 250px;
  vertical-align: middle;
  line-height: 250px;
  font-size: 100px;
  text-align: center;
}

.message {
  font-size: 50px!important;
  text-transform: uppercase;
  cursor: pointer;
}

.video-mask {
width: 260px;
-webkit-mask-image: -webkit-radial-gradient(circle, white 100%, black 100%);
-webkit-transform: rotate(0.000001deg);
border-radius: 260px;
overflow: hidden;
position: absolute;
left: 50%;
height: 260px;
}


.gradientGreen {
  height: 100vh;
  background: #11998e;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to bottom, #38ef7d, #11998e);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#38ef7d), to(#11998e));
  background: -webkit-linear-gradient(top, #38ef7d, #11998e);
  background: -o-linear-gradient(top, #38ef7d, #11998e);
  background: linear-gradient(to bottom, #38ef7d, #11998e); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  -webkit-transition: background-color 1000ms linear;
  -o-transition: background-color 1000ms linear;
  transition: background-color 1000ms linear;
}

.gradientRed {
  height: 100vh;
  background: #FF416C;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #FF4B2B, #FF416C);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#FF4B2B), to(#FF416C));
  background: -webkit-linear-gradient(left, #FF4B2B, #FF416C);
  background: -o-linear-gradient(left, #FF4B2B, #FF416C);
  background: linear-gradient(to right, #FF4B2B, #FF416C); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  -webkit-transition: background-color 1000ms linear;
  -o-transition: background-color 1000ms linear;
  transition: background-color 1000ms linear;
}

.buttonRecord {
  position: absolute;
  bottom: 20px;
  right: 20px;
}

#myVideo, #myPicture {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%;
  min-height: 100%;
}

.gn{
  position: relative;
  margin: 5% auto;
  background-color: #FE2222;
  border-radius: 50%;
  width: 5em;
  height: 5em;
  font-size: 15px;
}
:before, :after{
  content:'';
  position:absolute;
  background-color:#fff;
}
.gn:after{
  top:30%; left:43%;
  height:15%; width:14%;
  border-top-left-radius:50%;
  border-top-right-radius:50%;
}
.gn:before{
  top:40%; left:43%;
  height:15%; width:14%;
  border-bottom-left-radius:50%;
  border-bottom-right-radius:50%;
}
.mc{
  position:absolute;
  top:50%; left:37%;
  height:24%;
  width:26.5%;
  overflow:hidden;
}
.mc:before{
  bottom:50%;
  width:100%; height:100%;
  -webkit-box-sizing:border-box;
          box-sizing:border-box;
  border-radius:50%;
  border:0.5em solid #fff;
  background:none;
}
.mc:after{
  top:50%; left:40%;
  width:20%; height:25%;
}



